type ITranslate = {
  language: keyof typeof Countries;
};

type ReturnDataCountry = {
  [key: string]: {
    name: string;
    code: string;
    phoneCode: string;
    currencyId: string;
    id: string;
    createdAt: string;
    updatedAt: string;
  };
};

export const translateCountry = ({ language }: ITranslate): ReturnDataCountry =>
  Countries?.[language ?? 'es'];

export default translateCountry;
